.wrapperMainAvatar {
    position: relative;
}
.topSectionProfile{
    display: flex;
}
.mainAvatar {
    width: 210px;
    height: 250px;
    object-fit: cover;
    border-top-left-radius: 17px;
}
/* BUTTON EDIT AVATAR */
.file {
  display: none;
}
.buttonEditMainAvatar label{
  position: absolute;
  top: 5px;
  left: 5px;
  width: 27px;
  height: 27px;
  border-radius: 25px;
  background: #fff;
  box-shadow: 0 4px 7px rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  cursor: pointer;
}
.buttonEditMainAvatar label img {
    width: 17px;
    height: 17px;
}
/* STATUS */
.wrapperBodyStatus{
    margin: 5px 0px 0px 0px;
}
.textStatus{
    font-size: 15px;
    cursor: text;
}
.textStatus img{
    cursor: pointer;
}
.wrapperTextStatus{
    cursor: default;
}
.wrapperTextStatus img{
    width: 12px;
    height: 12px;
    margin: 0px 0px 0px 5px;
}
.addStatus{
    font-size: 12px;
    font-weight: 500;
    color: rgb(114, 114, 114);
}
/*  */

@media(max-width: 767px){
    .mainAvatar{
        width: 120px;
        height: 120px;
        border-radius: 50%;
    }
}

@media(max-width: 424px){
    .mainAvatar{
        width: 100px;
        height: 100px;
    }
}